
body {
  background: #f9f9f9;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
a {
  color: initial;
}
::-ms-input-placeholder {
  color: #a8a8a8;
}
::-webkit-input-placeholder {
  color: #a8a8a8;
}
::-moz-placeholder {
  color: #a8a8a8;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.flex-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.flex_1 {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.flex-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.flex-center-y {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.flex-center-x {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.flex-between {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
input {
  border: 0;
}
.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.turntable {
  background: rgba(0, 0, 0, .4);
}
.van-tabs__content {
  min-height: 80vh;
}
.tabbar {
  -webkit-box-shadow: 0 0 0.266667rem #eeecec;
  box-shadow: 0 0 0.266667rem #eeecec;
}
.tabbar .van-tabbar-item--active {
  color: #2194e6;
}
.tabbar .van-tabbar-item__text {
  margin-top: 0.08rem;
}
.container {
  padding-bottom: 1.466667rem;
}
.tabbar {
  height: 1.466667rem;
}
.cart .van-stepper__minus,
.cart .van-stepper__plus {
  border-radius: 50%;
  width: 0.613333rem;
  height: 0.613333rem;
  background: #f2f2f2;
}
.cart .van-stepper__input {
  border: 0;
  width: 0.8rem;
  height: 0.533333rem;
}
.cart .van-stepper {
  border: 0.026667rem solid #ececec;
  border-radius: 0.4rem;
  padding: 0.066667rem 0.106667rem;
}
.cart .balance .van-checkbox__icon {
  height: auto;
  margin-top: 0.133333rem;
}
.cart .van-checkbox__label {
  color: #707070;
}
.star .van-rate__item {
  padding: 0 0.026667rem;
}
.tab .van-tabs__wrap {
  overflow-x: auto;
  margin: 0 0.4rem;
}
.tab .van-tabs__nav--card {
  border: 0;
  background: transparent;
  display: -webkit-box;
  margin: 0;
}
.tab .van-tabs__nav--card .van-tab {
  -webkit-box-flex: inherit;
  -ms-flex: inherit;
  flex: inherit;
  padding: 0 0.48rem;
  border-radius: 0.8rem;
  border: 0.026667rem solid #eee;
  color: #545454;
  margin-right: 0.186667rem;
  font-size: 0.373333rem;
  border-right: none;
}
.tab .van-tabs__nav--card .van-tab--active {
  background: -webkit-gradient(linear, left top, right top, from(#21cce6) , to(#2696e6));
  background: linear-gradient(to right, #21cce6 , #2696e6);
  color: #fff;
}
.bindSettings {
  min-height: 100vh;
  background: #fff;
  overflow: hidden;
}
.mod_address .van-radio__input {
  color: #9e9e9e;
}
.checkbox_wrap .van-checkbox__icon--round .van-icon {
  border-radius: 0.08rem;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #313131;
  font-size: 0.373333rem;
}
#app .van-nav-bar .van-icon {
  color: #fff;
}
#app .van-nav-bar {
  height: 1.333333rem;
  line-height: 1.333333rem;
  background-color: #15181d;
  letter-spacing: 0.026667rem;
}
#app .van-loading {
  position: fixed;
  z-index: 1000;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  padding: 0.213333rem;
  border-radius: 0.106667rem;
  background: rgba(0, 0, 0, 0.5);
}

